.main-menu {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 3333;
    transform: translateX(-100%);
    transition: 1s;
    background-color: #fff;

    &__top {
        margin-bottom: 38px;
        margin-top: 40px;
        display: flex;
        justify-content: space-between;

        @include media ('>=md') {
            justify-content: flex-start;
        }
    }
}

.header__lists {
    display: flex;
}

.close {
    width: 34px;
    height: 34px;
    cursor: pointer;

    @include media ('>=md') {
        margin-right: 39px;
    }
}

.logo-mobile {
    align-self: center;
}

.mobile-list {
    list-style-type: none;
}

.mobile-list:nth-child(2) {
    display: none;

    @include media ('>=md') {
        display: block;
    }
}

.mobile-link {
    font-family: $font-header;
    color: $color-title;
    text-transform: uppercase;
    font-weight: 400;
    font-size: 18px;
    line-height: 19px;
    transition: 0.5s;

    &:hover {
        color: #4567AA;
        transition: 0.5s;
    }
}

.mobile-item {
    margin-bottom: 16px;
}

.click {
    background: #4567AA;
    border: 1px solid transparent;
    border-radius: 2px;
    text-transform: uppercase;
    color: #fff;
    display: block;
    margin: 0 auto;
    padding: 15px 77px;
    position: relative;
    margin-bottom: 59px;
    transition: 0.5s;
    width: 100%;

    @include media('>=md') {
        float: right;
    }

    &::after {
        position: absolute;
        bottom: -39px;
        left: 0;
        right: 0;
        content: '';
        background: #4567AA;
        height: 1.5px;
    }

    &:hover {
        background: #FF8139;
        transition: 0.5s;
    }
}

.phone {
    color: #4567AA;
    font-size: 16px;
    font-weight: 400;
    font-family: $font-text;
    margin-bottom: 6px;
    position: relative;

    &::before {
        position: absolute;
        top: -3px;
        left: -20px;
        content: '\e80e';
        font-family: 'fontello';
        font-size: 20px;
    }
}

.adress {
    font-family: $font-header;
    font-size: 16px;
    color: #4567AA;
    font-weight: 400;
    margin-bottom: 20px;
}

.social-link {
    list-style-type: none;
    display: flex;
}

.social-item:nth-child(3) {
    position: relative;
    margin-right: 30px;

    &::after {
        position: absolute;
        top: 0;
        left: 0;
        content: '\e806';
        font-family: 'fontello';
        font-size: 30px;
        color: #4567AA;
    }
}

.icon-vk {
    font-size: 30px;
    color: #4567AA;
    margin-right: 30px;
}

.icon-youtube {
    font-size: 30px;
    color: #4567AA;
    margin-right: 30px;
}

.main-menu--active {
    transform: translateX(0);
}

.safe-list {
    list-style-type: none;
    display: none;
    z-index: 1000;
    transition: 0.5s;
    position: absolute;
    bottom: -182px;
    left: 0;
    background: #fff;
}

.mobile-item-icon {
    position: relative;
    margin-bottom: 16px;
    transition: 0.5s;

    &::after {
        position: absolute;
        top: 4px;
        right: 130px;
        content: '\e809';
        font-family: 'fontello';
        font-size: 10px;
        transition: 0.5s;
    }

    &:hover .safe-list {
        display: block;
        transition: 0.5s;
    }

    &:hover::after {
        transform: rotate(180deg);
        transition: 0.5s;
    }

    &:hover {
        color: #4567AA;
        transition: 0.5s;
    }
}

.contacts {
    @include media ('>=md') {
        display: flex;
        width: 100%;
        justify-content: space-between;
    }

}