$duration: .15s;
$color-text: #333;
$color-title: #202020;

// Стек шрифтов для текста
$font-text: 'HandMadurai', sans-serif !default;

// Стек шрифтов для заголовков
$font-header: 'Forum', sans-serif !default;


// bootstrap

$font-family-base: sans-serif;
$font-size-base: 1rem;
$font-weight-base: 400;
$line-height-base: 1.5;

$enable-shadows: false;

$link-color: inherit;
$link-decoration: none;
$link-hover-color: inherit;
$link-hover-decoration: none;

$paragraph-margin: 2rem;

$grid-breakpoints: (
  xs: 0,
  sm: 360px,
  md: 768px,
  lg: 1366px,
  xl: 1920px
);

$container-max-widths: (
  sm: 330px,
  md: 678px,
  lg: 1220px,
  xl: 1680px
);

$grid-columns: 12;
$grid-gutter-width: 30px;

// include-media
$breakpoints: map-remove($grid-breakpoints, xs);

$media-expressions: (
  'ie': 'screen and (-ms-high-contrast: active), (-ms-high-contrast: none)'
);
