.footer__inner {
    background-color: #F5F5F5;
    display: flex;
    justify-content: center;
    width: 100%;
}

.footer__logo {
    display: block;

    padding-top: 40px;
    margin-bottom: 26px;

    @include media ('>=lg') {
        padding: 0;
        margin-left: 50px;
    }
}

.footer__item {
    @include media ('>=lg') {
        padding-top: 70px;
        width: 206px;
        height: 42px;
    }
}

.footer__item:nth-child(2) {

    @include media ('>=lg') {
        margin-left: 90px;
    }
}

.footer__container {
    width: 142px;
    margin: 0 auto;

    @include media (">=lg") {
        width: 750px;
        margin: 0;
    }
}

.adress__footer {
    margin-top: 26px;
    font-family: $font-header;
    font-size: 16px;
    line-height: 22px;
    color: $color-title;

    text-align: center;

    @include media ('>=md') {
        width: 100%;
        text-align: left;
        font-style: 14px;
    }
}

.phone {


    display: block;
    margin-left: 30px;

    @include media ('>=md') {
        width: 100%;
        margin: 0;
    }
}

.footer__menu {
    position: relative;
    display: block;
    width: 200px;
    height: 100%;
    margin: 0 auto;
    margin-top: 40px;
    color: #4567AA;
    font-family: $font-header;
    font-weight: 400;
    font-size: 16px;
    text-transform: uppercase;
    text-align: center;

    @include media ('>=md') {
        display: none;
    }

    &::before {
        position: absolute;
        top: -25px;
        left: 70px;
        content: url('../img/line-header.png');
    }

    &::after {
        position: absolute;
        top: -40px;
        left: 70px;
        content: url('../img/line-header.png');
    }
}

.copy {
    font-family: $font-text;
    font-size: 14px;
    color: #202020;
    opacity: 0.6;
}

.politics {
    font-family: $font-text;
    font-size: 14px;
    color: #202020;
    opacity: 0.6;
}

.mobile-link {
    font-size: 14px;
}

.footer__lists {
    display: none;

    @include media ('>=md') {
        display: block;
        padding-top: 30px;
    }
}

.mobile-link-footer {
    font-family: $font-header;
    font-size: 14px;
    color: #202020;
    text-transform: uppercase;
}

.safe-list-footer {
    list-style-type: none;
    display: none;
    z-index: 1000;
    transition: 0.5s;
    position: absolute;
    bottom: -182px;
    left: 0;
    background: #F5F5F5;
}

.more__one {
    display: block;

    @include media (">=md") {
        display: none;
    }
}

.more {
    display: none;

    @include media {
        display: block;
        display: flex;
        justify-content: space-between;
    }
}

.social-link-footer {
    list-style-type: none;

    @include media ('>=md') {
        display: none;
    }
}

.footer__lists {
    @include media ('>=lg') {
        padding-left: 250px;
        margin-top: 40px;
    }
}