.site-header {
    height: 40px;
}

.header__logo {
    position: relative;

    &::after {
        position: absolute;
        top: 9px;
        left: -15px;
        width: 30px;
        content: url('../img/line-header.png');
    }

    &::before {
        position: absolute;
        top: 0;
        left: -15px;
        width: 30px;
        content: url('../img/line-header.png');
    }
}

.header__inner {
    display: flex;
    justify-content: space-between;
}

.bron {
    background-color: #4567AA;
    width: 54px;
    height: 40px;
    margin-left: 20px;
    position: relative;
    transition: 0.3s;

    &::after {
        position: absolute;
        top: 5px;
        left: 17px;
        content: '\e801';
        font-family: 'fontello';
        color: #fff;
    }

    &:hover {
        background: #FF8139;
        transition: 0.3s;
    }
}

.logo {
    display: none;

}